.form {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid rgb(11, 37, 4);
    padding: 20px;
    width: 450px;
    margin: 0 auto;
  }
  
  .label {
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  
  .input {
    width: 250px;
    height: 30px;
    margin-bottom: 15px;
  }
  
  .button {
    padding: 10px 15px;
    width: 170px;
    height: 50px;
    font-size: 20px;
    border: none;
    border-radius: 15px;
    text-align: center;
  }